.sidebarOptions{
    display: flex;
    align-items: center;
    font-size: 12px;
    padding-left: 2px;
    cursor: pointer;
}

.sidebarOptions:hover{
    opacity: 0.9;
    background-color: #340e36;
}

.sidebarOptions__icon{
    padding: 10px;
    font-size: 15px !important;
}

.sidebarOptions__channel{
    padding: 10px 0; 
}

.sidebarOptions__hash{
    padding: 10px;
}

.sidebarOptions > h3{
    font-weight: 500;
}